import React from "react";
import MainToggleButton from "../main-toggle-button/main-toggle-button";

const ChatHeader = ({ name, username, image, }) => {
    return (<div className="chat-header">
        {/* <!-- Chat Back Button (Visible only in Small Devices) --> */}
        <MainToggleButton />

        {/* <!-- Chat participant's Name --> */}
        <div className="media chat-name align-items-center text-truncate ">
            <div className="avatar bg-success text-light d-none d-sm-inline-block mx-3 d-inline-block">
                <span>
                    {/* <svg className="hw-24" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"
                            d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z">
                        </path>
                    </svg> */}
                    <img className="injectable" src={image} alt="user" />
                </span>
            </div>

            <div className="media-body align-self-center ">
                <h6 className="text-truncate mb-0">{name}</h6>
                <small className="text-muted">@{username}</small>
            </div>
        </div>

        {/* <!-- Chat Options --> */}

    </div>)
}
export default ChatHeader;

